"use client";
import { useEffect, useState } from "react";
import { X } from "lucide-react";
import { Button } from "../ui/button";
import type { Student } from "@/app/(dashboard)/school/student/page";

type Props = {
  open: boolean;
  onClose: () => void;
  onAdd: () => Promise<void>;
  students: Student[];
  selected: string[];
  setSelected: (ids: string[]) => void;
};

export default function AddStudentModal({
  open,
  onClose,
  onAdd,
  students,
  selected,
  setSelected,
}: Props) {
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (open) setSelected([]);
  }, [open, setSelected]);

  const handleToggle = (id: string) => {
    setSelected(
      selected.includes(id)
        ? selected.filter((sid) => sid !== id)
        : [...selected, id]
    );
  };

  const handleAdd = async () => {
    setLoading(true);
    await onAdd();
    setLoading(false);
    onClose();
  };

  if (!open) return null;
  return (
    <div className="fixed inset-0 z-50 bg-black/30 flex items-center justify-center">
      <div className="bg-white rounded-xl p-6 shadow-lg relative w-[400px] max-w-[95vw]">
        <button
          onClick={onClose}
          className="absolute top-3 right-3 text-gray-400 hover:text-black"
          aria-label="Close"
        >
          <X size={20} />
        </button>
        <h2 className="text-lg font-semibold mb-4 text-center">បញ្ចូលសិស្ស</h2>
        <div className="mb-4 flex flex-wrap gap-2">
          {selected.map((id) => {
            const stu = students.find((s) => s.id === id);
            if (!stu) return null;
            return (
              <span
                key={id}
                className="bg-blue-100 text-blue-700 px-2 py-1 rounded-full text-xs flex items-center gap-1"
              >
                {stu.name}
                <button
                  type="button"
                  className="ml-1 text-blue-500 hover:text-red-500"
                  onClick={() => handleToggle(id)}
                >
                  <X size={12} />
                </button>
              </span>
            );
          })}
        </div>
        <div className="max-h-56 overflow-y-auto border rounded mb-4">
          {students.map((stu) => (
            <div
              key={stu.id}
              className="flex items-center gap-2 px-3 py-2 hover:bg-gray-50 cursor-pointer"
              onClick={() => handleToggle(stu.id)}
            >
              <input
                type="checkbox"
                checked={selected.includes(stu.id)}
                readOnly
                className="accent-blue-600"
              />
              <span>{stu.name}</span>
            </div>
          ))}
        </div>
        <Button
          className="w-full bg-[#25388C] hover:bg-[#1e2e6d] text-white"
          onClick={handleAdd}
          disabled={loading || selected.length === 0}
        >
          {loading ? "កំពុងបញ្ចូល..." : "បញ្ចូល"}
        </Button>
      </div>
    </div>
  );
}